import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import LayerTest from '../test/LayerTest';

export default new Router({
  routes: [{
    path: '/',
    redirect: 'Layer'
  }, {
    path: '/layer',
    name: 'Layer',
    component: r => require.ensure([], () => r(require('../docs/layer.md')))
  }, {
    path: '/test',
    name: 'Test',
    component: LayerTest
  }]
});



